// src/pages/admin/AdminNotices.js
import React, { useState, useEffect, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Checkbox from '@mui/material/Checkbox';
import FormControlLabel from '@mui/material/FormControlLabel';
import IconButton from '@mui/material/IconButton';

// ì¬ì‚¬ìš© ê°€ëŠ¥í•œ ê¸°ì¡´ ì»´í¬ë„ŒíŠ¸ import
import MarkdownEditor from '../../components/posts/upload/MarkdownEditor';
import FileUploader from '../../components/posts/upload/FileUploader';
import LoadingSpinner from '../../components/common/LoadingSpinner';

// ì•„ì´ì½˜
import AddIcon from '@mui/icons-material/Add';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import EyeIcon from '@mui/icons-material/Visibility';

// Firebase
import { useAuth } from '../../components/auth/AuthContext';
import { 
  collection, 
  query, 
  getDocs, 
  doc, 
  getDoc, 
  setDoc, 
  deleteDoc, 
  updateDoc,
  serverTimestamp,
  orderBy
} from 'firebase/firestore';
import { ref, uploadBytes, getDownloadURL } from 'firebase/storage';
import { db, storage } from '../../firebase';
import ReactMarkdown from 'react-markdown';

const AdminNotices = () => {
  const navigate = useNavigate();
  const { currentUser } = useAuth();
  const [notices, setNotices] = useState([]);
  const [loading, setLoading] = useState(true);
  const [dialogOpen, setDialogOpen] = useState(false);
  const [editingNotice, setEditingNotice] = useState(null);
  const [noticeTitle, setNoticeTitle] = useState('');
  const [noticeContent, setNoticeContent] = useState('');
  const [isMainPageNotice, setIsMainPageNotice] = useState(false);
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [noticeToDelete, setNoticeToDelete] = useState(null);
  const [saving, setSaving] = useState(false);
  const [files, setFiles] = useState([]);
  const textAreaRef = useRef(null);
  const [previewMode, setPreviewMode] = useState(false);

  // ê´€ë¦¬ì ê¶Œí•œ í™•ì¸
  useEffect(() => {
    if (currentUser && currentUser.role !== 'ADMIN') {
      alert('ê´€ë¦¬ì ê¶Œí•œì´ í•„ìš”í•©ë‹ˆë‹¤.');
      navigate('/');
    }
  }, [currentUser, navigate]);

  // ê³µì§€ì‚¬í•­ ëª©ë¡ ë¶ˆëŸ¬ì˜¤ê¸°
  useEffect(() => {
    const fetchNotices = async () => {
      if (!currentUser) return;
      
      try {
        setLoading(true);
        const noticesRef = collection(db, 'notices');
        const q = query(noticesRef, orderBy('createdAt', 'desc'));
        const querySnapshot = await getDocs(q);
        
        const noticesList = querySnapshot.docs.map(doc => ({
          id: doc.id,
          ...doc.data(),
          createdAt: doc.data().createdAt?.toDate?.() || new Date()
        }));
        
        setNotices(noticesList);
      } catch (error) {
        console.error('ê³µì§€ì‚¬í•­ ë¡œë“œ ì¤‘ ì˜¤ë¥˜:', error);
        alert('ê³µì§€ì‚¬í•­ì„ ë¶ˆëŸ¬ì˜¤ëŠ” ì¤‘ ì˜¤ë¥˜ê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤.');
      } finally {
        setLoading(false);
      }
    };
    
    fetchNotices();
  }, [currentUser]);

  // ê³µì§€ì‚¬í•­ ì¶”ê°€/ìˆ˜ì • ë‹¤ì´ì–¼ë¡œê·¸ ì—´ê¸°
  const handleOpenDialog = (notice = null) => {
    if (notice) {
      setEditingNotice(notice);
      setNoticeTitle(notice.title);
      setNoticeContent(notice.content);
      setIsMainPageNotice(notice.isMainPageNotice || false);
      // ê¸°ì¡´ íŒŒì¼ ì •ë³´ ì„¤ì •
      setFiles(notice.files || []);
    } else {
      setEditingNotice(null);
      setNoticeTitle('');
      setNoticeContent('');
      setIsMainPageNotice(false);
      setFiles([]);
    }
    setDialogOpen(true);
    setPreviewMode(false);
  };

  // ê³µì§€ì‚¬í•­ ì‚­ì œ ë‹¤ì´ì–¼ë¡œê·¸ ì—´ê¸°
  const handleOpenDeleteDialog = (notice) => {
    setNoticeToDelete(notice);
    setDeleteDialogOpen(true);
  };

  // ë“œë˜ê·¸ ì•¤ ë“œë¡­ìœ¼ë¡œ ë°›ì€ íŒŒì¼ ì²˜ë¦¬ (MarkdownEditorì—ì„œ í˜¸ì¶œ)
  const handleDrop = (e, textAreaRef) => {
    const files = Array.from(e.dataTransfer.files);
    const validFiles = files.filter(file => 
      file.type.startsWith('image/') || 
      file.type === 'application/pdf' ||
      file.type.includes('document')
    );

    if (validFiles.length > 0 && textAreaRef.current) {
      const cursorPosition = textAreaRef.current.selectionStart;
      
      for (const file of validFiles) {
        let markdown = '';
        let fileType = '';
        
        if (file.type.startsWith('image/')) {
          const tempUrl = URL.createObjectURL(file);
          markdown = `![${file.name}](${tempUrl})\n`;
          fileType = 'IMAGE';
        } else {
          const tempUrl = URL.createObjectURL(file);
          markdown = `[${file.type === 'application/pdf' ? 'PDF: ' : 'ë¬¸ì„œ: '}${file.name}](${tempUrl})\n`;
          fileType = file.type === 'application/pdf' ? 'PDF' : 'DOC';
        }
        
        const newContent = noticeContent.slice(0, cursorPosition) + 
                    markdown + 
                    noticeContent.slice(cursorPosition);
        
        setNoticeContent(newContent);
        
        // íŒŒì¼ ëª©ë¡ì— ì¶”ê°€
        handleAddFile({
          fileId: `file-${Date.now()}-${Math.random()}`,
          file: file,
          type: fileType,
          description: file.name,
          tempUrl
        });
      }
    }
  };

  // íŒŒì¼ ì¶”ê°€ (FileUploaderì—ì„œ í˜¸ì¶œ)
  const handleAddFile = (fileData) => {
    setFiles(prev => [...prev, fileData]);
  };

  // íŒŒì¼ ì„¤ëª… ì—…ë°ì´íŠ¸ (FileUploaderì—ì„œ í˜¸ì¶œ)
  const handleUpdateFileDescription = (fileId, description) => {
    setFiles(prev => prev.map(file => 
      file.fileId === fileId ? { ...file, description } : file
    ));
  };

  // íŒŒì¼ ì‚­ì œ (FileUploaderì—ì„œ í˜¸ì¶œ)
  const handleRemoveFile = (fileId) => {
    // íŒŒì¼ ëª©ë¡ì—ì„œ ì‚­ì œí•  íŒŒì¼ ì •ë³´ ì°¾ê¸°
    const fileToRemove = files.find(file => file.fileId === fileId);
    
    if (fileToRemove) {
      // í•´ë‹¹ íŒŒì¼ì˜ ë§ˆí¬ë‹¤ìš´ íŒ¨í„´ì„ ì°¾ì•„ ì‚­ì œ
      let updatedContent = noticeContent;
      
      if (fileToRemove.type === 'IMAGE') {
        // ì´ë¯¸ì§€ ë§ˆí¬ë‹¤ìš´ íŒ¨í„´: ![íŒŒì¼ì´ë¦„](URL)
        const imgPattern = new RegExp(`!\\[.*?\\]\\(${fileToRemove.tempUrl || fileToRemove.url}\\)`, 'g');
        updatedContent = updatedContent.replace(imgPattern, '');
      } else {
        // íŒŒì¼ ë§í¬ ë§ˆí¬ë‹¤ìš´ íŒ¨í„´: [íŒŒì¼ì´ë¦„](URL)
        const linkPattern = new RegExp(`\\[.*?\\]\\(${fileToRemove.tempUrl || fileToRemove.url}\\)`, 'g');
        updatedContent = updatedContent.replace(linkPattern, '');
      }
      
      setNoticeContent(updatedContent);
      setFiles(prev => prev.filter(file => file.fileId !== fileId));
    }
  };

  // ê³µì§€ì‚¬í•­ ì €ì¥
  const handleSaveNotice = async () => {
    if (!noticeTitle.trim() || !noticeContent.trim()) {
      alert('ì œëª©ê³¼ ë‚´ìš©ì„ ëª¨ë‘ ì…ë ¥í•´ì£¼ì„¸ìš”.');
      return;
    }
    
    try {
      setSaving(true);
      
      // 1. íŒŒì¼ ì—…ë¡œë“œ
      const uploadedFiles = await Promise.all(
        files.map(async (fileItem) => {
          // ì´ë¯¸ ì—…ë¡œë“œëœ íŒŒì¼ì€ ê·¸ëŒ€ë¡œ ì‚¬ìš©
          if (fileItem.url && !fileItem.file) {
            return fileItem;
          }
          
          // ìƒˆë¡œìš´ íŒŒì¼ ì—…ë¡œë“œ
          const fileRef = ref(storage, `notices/${currentUser.uid}/${Date.now()}-${fileItem.file.name}`);
          const fileSnapshot = await uploadBytes(fileRef, fileItem.file);
          const url = await getDownloadURL(fileSnapshot.ref);
          
          return {
            fileId: fileItem.fileId,
            url: url,
            filename: fileItem.file.name,
            type: fileItem.type,
            description: fileItem.description
          };
        })
      );
      
      // 2. ì„ì‹œ URLì„ ì‹¤ì œ URLë¡œ ë³€ê²½
      let updatedContent = noticeContent;
      
      files.forEach((fileItem) => {
        if (fileItem.tempUrl) {
          const uploadedFile = uploadedFiles.find(f => f.fileId === fileItem.fileId);
          
          if (uploadedFile) {
            // ì„ì‹œ URLì„ ì‹¤ì œ ì—…ë¡œë“œëœ URLë¡œ êµì²´
            if (fileItem.type === 'IMAGE') {
              // ì´ë¯¸ì§€ ë§ˆí¬ë‹¤ìš´ íŒ¨í„´: ![íŒŒì¼ì´ë¦„](tempUrl)
              const imgPattern = new RegExp(`!\\[.*?\\]\\(${fileItem.tempUrl}\\)`, 'g');
              updatedContent = updatedContent.replace(imgPattern, `![${uploadedFile.filename}](${uploadedFile.url})`);
            } else {
              // íŒŒì¼ ë§í¬ ë§ˆí¬ë‹¤ìš´ íŒ¨í„´: [íŒŒì¼ì´ë¦„](tempUrl)
              // ë‹¤ìš´ë¡œë“œë¥¼ ìœ„í•´ íŠ¹ë³„í•œ í˜•ì‹ìœ¼ë¡œ í‘œì‹œ
              const linkPattern = new RegExp(`\\[.*?\\]\\(${fileItem.tempUrl}\\)`, 'g');
              updatedContent = updatedContent.replace(linkPattern, `[${uploadedFile.filename}](${uploadedFile.url} "download")`);
            }
          }
        }
      });
      
      // 3. ê³µì§€ì‚¬í•­ ë°ì´í„° ì¤€ë¹„
      const noticeData = {
        title: noticeTitle,
        content: updatedContent,
        isMainPageNotice,
        authorId: currentUser.uid,
        files: uploadedFiles,
        updatedAt: serverTimestamp()
      };
      
      // 4. ê³µì§€ì‚¬í•­ ì €ì¥ ë˜ëŠ” ìˆ˜ì •
      if (editingNotice) {
        // ìˆ˜ì •
        await updateDoc(doc(db, 'notices', editingNotice.id), noticeData);
        
        // ìƒíƒœ ì—…ë°ì´íŠ¸
        setNotices(prev => prev.map(notice => 
          notice.id === editingNotice.id 
            ? { ...notice, ...noticeData, updatedAt: new Date() } 
            : notice
        ));
      } else {
        // ìƒˆ ê³µì§€ì‚¬í•­
        noticeData.createdAt = serverTimestamp();
        
        // ìƒˆ ê³µì§€ì‚¬í•­ ì¶”ê°€
        const newNoticeRef = doc(collection(db, 'notices'));
        await setDoc(newNoticeRef, noticeData);
        
        // ìƒíƒœ ì—…ë°ì´íŠ¸
        setNotices(prev => [
          {
            id: newNoticeRef.id,
            ...noticeData,
            createdAt: new Date(),
            updatedAt: new Date()
          },
          ...prev
        ]);
      }
      
      setDialogOpen(false);
      setSaving(false);
    } catch (error) {
      console.error('ê³µì§€ì‚¬í•­ ì €ì¥ ì¤‘ ì˜¤ë¥˜:', error);
      alert('ê³µì§€ì‚¬í•­ì„ ì €ì¥í•˜ëŠ” ì¤‘ ì˜¤ë¥˜ê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤.');
      setSaving(false);
    }
  };

  // ê³µì§€ì‚¬í•­ ì‚­ì œ
  const handleDeleteNotice = async () => {
    if (!noticeToDelete) return;
    
    try {
      await deleteDoc(doc(db, 'notices', noticeToDelete.id));
      
      // ìƒíƒœ ì—…ë°ì´íŠ¸
      setNotices(prev => prev.filter(notice => notice.id !== noticeToDelete.id));
      
      setDeleteDialogOpen(false);
      setNoticeToDelete(null);
    } catch (error) {
      console.error('ê³µì§€ì‚¬í•­ ì‚­ì œ ì¤‘ ì˜¤ë¥˜:', error);
      alert('ê³µì§€ì‚¬í•­ì„ ì‚­ì œí•˜ëŠ” ì¤‘ ì˜¤ë¥˜ê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤.');
    }
  };

  // ë¯¸ë¦¬ë³´ê¸° ëª¨ë“œ í† ê¸€
  const togglePreviewMode = () => {
    setPreviewMode(!previewMode);
  };

  if (!currentUser) {
    return <LoadingSpinner message="ë¡œê·¸ì¸ ìƒíƒœë¥¼ í™•ì¸í•˜ëŠ” ì¤‘..." />;
  }

  if (currentUser.role !== 'ADMIN') {
    return null;
  }

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Box sx={{ mb: 4, display: 'flex', alignItems: 'center' }}>
        <IconButton 
          onClick={() => navigate('/admin')} 
          sx={{ mr: 2 }}
        >
          <ArrowBackIcon />
        </IconButton>
        <Typography variant="h4" component="h1" sx={{ flexGrow: 1 }}>
          ê³µì§€ì‚¬í•­ ê´€ë¦¬
        </Typography>
        <Button
          variant="contained"
          startIcon={<AddIcon />}
          onClick={() => handleOpenDialog()}
          sx={{ 
            bgcolor: '#0066CC',
            '&:hover': {
              bgcolor: '#004C99'
            }
          }}
        >
          ê³µì§€ì‚¬í•­ ì‘ì„±
        </Button>
      </Box>

      {loading ? (
        <LoadingSpinner message="ê³µì§€ì‚¬í•­ì„ ë¶ˆëŸ¬ì˜¤ëŠ” ì¤‘..." />
      ) : (
        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow sx={{ backgroundColor: '#f5f5f5' }}>
                <TableCell width="50%">ì œëª©</TableCell>
                <TableCell>ì‘ì„±ì¼</TableCell>
                <TableCell>ë©”ì¸í‘œì‹œ</TableCell>
                <TableCell align="right">ê´€ë¦¬</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {notices.length === 0 ? (
                <TableRow>
                  <TableCell colSpan={4} align="center" sx={{ py: 3 }}>
                    ë“±ë¡ëœ ê³µì§€ì‚¬í•­ì´ ì—†ìŠµë‹ˆë‹¤.
                  </TableCell>
                </TableRow>
              ) : (
                notices.map((notice) => (
                  <TableRow key={notice.id} hover>
                    <TableCell>{notice.title}</TableCell>
                    <TableCell>
                      {notice.createdAt.toLocaleDateString('ko-KR')}
                    </TableCell>
                    <TableCell>
                      {notice.isMainPageNotice ? 'âœ“' : ''}
                    </TableCell>
                    <TableCell align="right">
                      <IconButton 
                        onClick={() => handleOpenDialog(notice)}
                        size="small"
                        sx={{ color: 'primary.main' }}
                      >
                        <EditIcon />
                      </IconButton>
                      <IconButton 
                        onClick={() => handleOpenDeleteDialog(notice)}
                        size="small"
                        sx={{ color: 'error.main' }}
                      >
                        <DeleteIcon />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                ))
              )}
            </TableBody>
          </Table>
        </TableContainer>
      )}
      
      {/* ê³µì§€ì‚¬í•­ ì¶”ê°€/ìˆ˜ì • ë‹¤ì´ì–¼ë¡œê·¸ */}
      <Dialog 
        open={dialogOpen} 
        onClose={() => !saving && setDialogOpen(false)}
        fullWidth
        maxWidth="md"
      >
        <DialogTitle>
          {editingNotice ? 'ê³µì§€ì‚¬í•­ ìˆ˜ì •' : 'ìƒˆ ê³µì§€ì‚¬í•­ ì‘ì„±'}
        </DialogTitle>
        <DialogContent>
          <Box sx={{ mb: 3, mt: 1 }}>
            <TextField
              fullWidth
              label="ì œëª©"
              value={noticeTitle}
              onChange={(e) => setNoticeTitle(e.target.value)}
              required
            />
          </Box>
          
          <FormControlLabel
            control={
              <Checkbox
                checked={isMainPageNotice}
                onChange={(e) => setIsMainPageNotice(e.target.checked)}
              />
            }
            label="ë©”ì¸ í˜ì´ì§€ì— í‘œì‹œ"
          />
          
          <Box sx={{ display: 'flex', justifyContent: 'flex-end', mt: 2, mb: 2 }}>
            <Button
              variant="outlined"
              startIcon={<EyeIcon />}
              onClick={togglePreviewMode}
            >
              {previewMode ? "ì—ë””í„°ë¡œ ëŒì•„ê°€ê¸°" : "ë¯¸ë¦¬ë³´ê¸°"}
            </Button>
          </Box>

          {!previewMode ? (
            // ë§ˆí¬ë‹¤ìš´ ì—ë””í„° ì»´í¬ë„ŒíŠ¸ ì‚¬ìš©
            <MarkdownEditor
              value={noticeContent}
              onChange={setNoticeContent}
              onDrop={handleDrop}
              placeholder="ê³µì§€ì‚¬í•­ ë‚´ìš©ì„ ì…ë ¥í•´ì£¼ì„¸ìš”. ì´ë¯¸ì§€ë‚˜ íŒŒì¼ì„ ë“œë˜ê·¸í•˜ì—¬ ì¶”ê°€í•  ìˆ˜ ìˆìŠµë‹ˆë‹¤."
            />
          ) : (
            <Box sx={{
              height: '400px',
              overflow: 'auto',
              p: 3,
              border: '1px solid',
              borderColor: 'divider',
              borderRadius: 1,
              bgcolor: 'grey.50'
            }}>
              <ReactMarkdown>{noticeContent}</ReactMarkdown>
            </Box>
          )}

          {/* íŒŒì¼ ì—…ë¡œë” ì»´í¬ë„ŒíŠ¸ ì‚¬ìš© */}
          <FileUploader
            files={files}
            onAddFiles={handleAddFile}
            onUpdateDescription={handleUpdateFileDescription}
            onRemoveFile={handleRemoveFile}
          />
        </DialogContent>
        <DialogActions>
          <Button 
            onClick={() => setDialogOpen(false)}
            disabled={saving}
          >
            ì·¨ì†Œ
          </Button>
          <Button 
            onClick={handleSaveNotice}
            variant="contained"
            disabled={saving}
          >
            {saving ? 'ì €ì¥ ì¤‘...' : 'ì €ì¥'}
          </Button>
        </DialogActions>
      </Dialog>
      
      {/* ì‚­ì œ í™•ì¸ ë‹¤ì´ì–¼ë¡œê·¸ */}
      <Dialog
        open={deleteDialogOpen}
        onClose={() => setDeleteDialogOpen(false)}
      >
        <DialogTitle>ê³µì§€ì‚¬í•­ ì‚­ì œ</DialogTitle>
        <DialogContent>
          <DialogContentText>
            ê³µì§€ì‚¬í•­ "{noticeToDelete?.title}"ì„(ë¥¼) ì‚­ì œí•˜ì‹œê² ìŠµë‹ˆê¹Œ?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setDeleteDialogOpen(false)}>
            ì·¨ì†Œ
          </Button>
          <Button 
            onClick={handleDeleteNotice} 
            color="error"
            autoFocus
          >
            ì‚­ì œ
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default AdminNotices;